body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#header {
  background-color: #00B7FF;
}
.headBanner{
  background-color: #351220;
  background-image: url(/static/media/banner.90446450.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 30vw;
}

.ant-select-show-search{
  margin: 1rem !important;
}

.cards{
  width: 90%;
  margin: 0 auto;

}
.singleCard{
  width: 250px;
  flex-grow: 1;
}

.boxCard{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.ant-btn-primary, .ant-btn{
  background-color: #350d23 !important;
  color: white !important;
  border: none !important;
  margin-top: 1rem !important;
}
.ant-btn:hover, .ant-btn:focus{
  background-color: #1F0714 !important;
  font-weight: bold !important;
}

.ant-tabs-tab:hover, .ant-tabs-tab:focus, .ant-tabs-tab:active,.ant-tabs-tab-active{
  color: #350d23 !important;
}
